.navbar {
    height: 50px;
    border-bottom: 0.5px solid rgb(218, 213, 213);
    display: flex;
    align-items: center;
    font-size: 15px;
    color: #555;

    .wrapper {
        width: 100%;
        display: flex;
        padding: 20px;
        align-items: center;
        justify-content: space-between;

        .search {
            display: flex;
            align-items: center;
            border: 0.5px solid lightgrey;
            padding: 5px;

            input {
                border: none;
                outline: none;
                background: transparent;
                &::placeholder {
                    font-size: 12px;
                }
            }
        }

        .items {
            display: flex;
            align-items: center;

            .item {
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;

                .icon {
                    font-size: 20px;
                }
                .avatar {
                    height: 30px;
                    width: 30px;
                    border-radius: 50%;
                }
                .counter {
                    width: 15px;
                    height: 15px;
                    background-color: red;
                    color: white;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 10px;
                    font-weight: bold;
                    position: absolute;
                    top: -5px;
                    right: -5px;
                }
            }
        }
    }
}
